.gameContainer {
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
    height: 100%;
    background-color: rgba(33,33,33 ,1);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    overflow: auto;
}

.fireButtonContainer {
    height: 100%;
    width: 10%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
}

.fireButton {
    width: 60%;
    padding-bottom: 57%;
    margin-bottom: 15%;
    box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.9);
    border: solid 0.5vh rgba(255, 255, 255, 0);
    border-radius: 50vw;
    cursor: pointer;


    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}

.fireButton:active {
    background-color: rgba(255, 255, 255, 0.5);
    border: solid 0.5vh rgba(255, 255, 255, 1);
}

.fireButton:focus {
    outline: none;
  /* background: none; */
}

.invulnerabilityButtonContainer {
    height: 100%;
    width: 10%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
}

.invulnerabilityButton {
    width: 60%;
    padding-bottom: 57.6%;
    box-sizing: border-box;
    margin-bottom: 15%;
    background-color: rgba(229,57,53 ,1);
    cursor: pointer;
    border: solid 0.5vh rgba(229,57,53 ,0);

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;

}

.invulnerabilityButton:active {
    background-color: rgba(229,57,53 ,0.5);
    border: solid 0.5vh rgba(229,57,53 ,1);
}

.invulnerabilityButton:focus {
    outline: none;
    /* background: none; */
}

@media (orientation: landscape) {
    .responsiveButton {
        display: none;
    }
}

@media (orientation: portrait) {

    .gameContainer {
        flex-direction: column;
    }

    .buttonsGeneralContainer {
        width: 100%;
        height: 10%;
        flex-grow: 1;
        display: flex;
        flex-direction: row;
    }

    .fireButtonContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .invulnerabilityButtonContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .noResponsiveButton {
        display: none;
    }

}